import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import {
  ProfileContainer,
  ProfileHeader,
  ProfileImage,
  ProfileName,
  ProfileSection,
  ProfileLabel,
  ProfileEdit,
  ProfileDelete,
  ProfileLogout,
} from "./ProfilePageStyle";
import ProfileModal from "./ProfileModal"; //   
import { fetchEditUserData, deleteUser } from "../../api/profile"; //  api  

const Profile = () => {
  const [user, setUser] = useState({
    id: 1,
    name: "",
    email: "elice@test.com",
    profileImage:
      "https://a0.muscache.com/im/pictures/hosting/Hosting-U3RheVN1cHBseUxpc3Rpbmc6MTEyNjE4NTg5MzIzNjI0NjI2MA%3D%3D/original/e6b26733-2c15-47d9-b097-6968b39bb697.jpeg?im_w=1440&im_q=highq",
  });
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false); //   

  const navigate = useNavigate(); //    

  useEffect(() => {
    /**     */
    const getUserData = async () => {
      try {
        const userData = await fetchEditUserData();
        setUser(userData);
      } catch (error) {
        console.error("    .", error);
      }
    };

    getUserData();
  }, []);

  /**     */
  const onClickHandleProfileEdit = () => {
    navigate(`/profile/edit/${user.id}`);
  };

  /**        */
  const onClickHandleProfileDelete = () => {
    setIsDeleteModalOpen(true);
  };

  /**    */
  const onClickHandleCancelDelete = () => {
    setIsDeleteModalOpen(false);
  };

  /**    */
  const onClickHandleConfirmDelete = async () => {
    try {
      await deleteUser(user.id); //      
      setIsDeleteModalOpen(false);
      navigate("/"); //  
    } catch (error) {
      console.error("  .", error);
    }
  };

  /**       */
  const onClickHandleProfileLogout = () => {
    // ?
    console.log("");
    navigate("/");
  };

  return (
    <ProfileContainer>
      <ProfileHeader>
        <ProfileImage src={user.profileImage} alt="Profile" />
        <ProfileName>{user.name}</ProfileName>
      </ProfileHeader>
      <ProfileSection>
        <ProfileLabel onClick={onClickHandleProfileEdit}> </ProfileLabel>
        <ProfileEdit onClick={onClickHandleProfileEdit}>
          <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="#333">
            <path d="M560-240 320-480l240-240 56 56-184 184 184 184-56 56Z" />
          </svg>
        </ProfileEdit>
      </ProfileSection>
      <ProfileSection>
        <ProfileDelete onClick={onClickHandleProfileDelete}> </ProfileDelete>
      </ProfileSection>
      <ProfileLogout onClick={onClickHandleProfileLogout}></ProfileLogout>

      {isDeleteModalOpen && (
        <ProfileModal
          message=" ?"
          onConfirm={onClickHandleConfirmDelete}
          onCancel={onClickHandleCancelDelete}
        />
      )}
    </ProfileContainer>
  );
};

export default Profile;
