//   
import React from "react";
import DatePicker from "react-datepicker";
import { ko } from "date-fns/locale";
import {
  Flexbox,
  ColumnBox,
  SectionDetail,
  SectionDetailBold,
  ModifyButton,
} from "./ReservationStyle";

const ReservationInfo = ({
  dateRange,
  nights,
  showDatePicker,
  setShowDatePicker,
  handleDateChange,
  guestCount,
  setShowGuestModal,
}) => {
  const today = new Date(); //    

  return (
    <>
      <Flexbox>
        <ColumnBox>
          <SectionDetailBold></SectionDetailBold>
          <SectionDetail>
            {dateRange[0].toLocaleDateString("ko-KR", {
              year: "numeric",
              month: "long",
              day: "numeric",
            })}{" "}
            ~{" "}
            {dateRange[1] &&
              dateRange[1].toLocaleDateString("ko-KR", {
                year: "numeric",
                month: "long",
                day: "numeric",
              })}
            ({nights})
          </SectionDetail>
        </ColumnBox>
        <ModifyButton onClick={() => setShowDatePicker(!showDatePicker)}>
          
        </ModifyButton>
      </Flexbox>
      {showDatePicker && (
        <DatePicker
          selected={dateRange[0]}
          onChange={handleDateChange}
          startDate={dateRange[0]}
          endDate={dateRange[1]}
          selectsRange
          locale={ko}
          inline
          minDate={today}
        />
      )}
      <Flexbox>
        <ColumnBox>
          <SectionDetailBold></SectionDetailBold>
          <SectionDetail> {guestCount}</SectionDetail>
        </ColumnBox>
        <ModifyButton onClick={() => setShowGuestModal(true)}>
          
        </ModifyButton>
      </Flexbox>
    </>
  );
};

export default ReservationInfo;
