import React, { useState, useEffect, useCallback, useMemo } from "react";
import { useNavigate, useParams } from "react-router-dom";
import {
  ProfileEditContainer,
  ProfileEditSection,
  ProfileEditForm,
  ProfileEditButtonContainer,
  ProfileEditSaveButton,
} from "./ProfileEditPageStyle";
import ProfileImageUpload from "./ProfileImageUpload"; //     
import ProfileModal from "./ProfileModal"; //   
import ProfileInput from "./ProfileInput"; //  input  
import { fetchUserData, editUserData } from "../../api/profile"; //  api  

/**        */
const phoneRegex = /^[0-9]{3}-[0-9]{4}-[0-9]{4}$/;

/**     */
const validatePassword = (password) => {
  if (password.length > 0 && password.length < 10) {
    return "10  .";
  } else {
    const hasLetter = /[a-zA-Z]/.test(password);
    const hasNumber = /[0-9]/.test(password);
    const hasSpecialChar = /[^a-zA-Z0-9]/.test(password);
    const isValidCombination = [hasLetter, hasNumber, hasSpecialChar].filter(Boolean).length >= 2;

    if (!isValidCombination) {
      return "//( ) , 2  ";
    } else {
      return "";
    }
  }
};

const ProfileEdit = () => {
  const [profileImage, setProfileImage] = useState(
    "https://a0.muscache.com/im/pictures/hosting/Hosting-U3RheVN1cHBseUxpc3Rpbmc6MTEyNjE4NTg5MzIzNjI0NjI2MA%3D%3D/original/e6b26733-2c15-47d9-b097-6968b39bb697.jpeg?im_w=1440&im_q=highq",
  );
  const [email, setEmail] = useState("elice@test.com");
  const [password, setPassword] = useState("");
  const [passwordCheck, setPasswordCheck] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [passwordCheckError, setPasswordCheckError] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("010-1234-5678");
  const [isModal, setIsModal] = useState(false);

  const { id } = useParams(); // url   id 
  const navigate = useNavigate();

  

  /**    */
  useEffect(() => {
    const getUserData = async () => {
      try {
        const userData = await fetchUserData(id);
        setProfileImage(userData.profileImage);
        setEmail(userData.email);
        setName(userData.name);
        setPhone(userData.phone);
      } catch (error) {
        console.error("   .");
      }
    };

    getUserData();
  }, [id]);

  /**    */
  const onChangeHandler = useCallback((e) => {
    const { name, value } = e.target;

    if (name === "phone") {
      const formattedValue = value.replace(/\D/g, "").slice(0, 11).replace(/(\d{3})(\d{4})(\d{4})/, "$1-$2-$3"); //       
      setPhone(formattedValue);
    } else if (name === "password") {
      setPassword(value);
      setPasswordError(validatePassword(value)); //   
    } else if (name === "passwordCheck") {
      setPasswordCheck(value);
      setPasswordCheckError(value !== password ? "  ." : ""); //    
    }
  },[password]);

  /**     */
  const onClickHandleSave = async (e) => {
    e.preventDefault();

    //        
    if (!password || !passwordCheck || !phone) {
      alert("  .");
      return;
    }

    if (passwordError || passwordCheckError) {
      return;
    }

    if (!phoneRegex.test(phone)) {
      alert("  000-0000-0000   .");
      return;
    }

    /**     */
    try {
      await editUserData(id, { password, phone, profileImage });
      setIsModal(true);
      navigate("/");
    } catch (error) {
      console.error("   .");
    }
    //   (await ~ console.error }    )
    //   console.log("", { password, phone, profileImage });
    //   setIsModal(true);
    // } catch (error) {
    //   console.error("   .", error);
    // }
  };

  /**    */
  const onClickHandleCloseModal = () => {
    setIsModal(false);
    navigate("/"); //      
  };

  return (
    <>
      <ProfileEditContainer>
        <ProfileEditSection>
          <ProfileImageUpload profileImage={profileImage} setProfileImage={setProfileImage} />
          <ProfileEditForm>
            <ProfileInput type="email"  name="email" value={email} disabled />
            <ProfileInput
              type="password"
              name="password"
              placeholder="  "
              value={password}
              required
              onChange={onChangeHandler}
              error={passwordError}
            />
            <ProfileInput
              type="password"
              name="passwordCheck"
              placeholder="  "
              value={passwordCheck}
              required
              onChange={onChangeHandler}
              error={passwordCheckError}
            />
            <ProfileInput type="text" name="name" value={name} disabled />
            <ProfileInput
              type="tel"
              name="phone"
              placeholder=" "
              maxLength={13}
              value={phone}
              required
              onChange={onChangeHandler} //      
            />
            <ProfileEditButtonContainer>
              <ProfileEditSaveButton onClick={onClickHandleSave}></ProfileEditSaveButton>
            </ProfileEditButtonContainer>
          </ProfileEditForm>
        </ProfileEditSection>
      </ProfileEditContainer>
      {isModal && <ProfileModal message=" !" onClose={onClickHandleCloseModal} />}
    </>
  );
};

export default ProfileEdit;
