import React, { useState, useEffect } from "react";
import { useSetRecoilState, useRecoilValue } from "recoil";
import "react-datepicker/dist/react-datepicker.css";
import Header from "../components/layout/SubHeader";
import Footer from "../components/layout/MainFooter";
import loginState from "../atoms/loginState";
import ReservationModal from "../components/reservation/ReservationModal";
import GuestModal from "../components/reservation/GuestModal";
import ReservationInfo from "../components/reservation/ReservationInfo";
import ReservationPrice from "../components/reservation/ReservationPrice";
import {
  ImagePlaceholder,
  Section,
  SectionTitle,
  Button,
  RefundPolicy,
  DetailContainer,
  Title,
  Name,
  Description,
  Container,
  RequestContainer,
} from "../components/reservation/ReservationStyle";

const ReservationPage = ({ title, description, name, initialStartDate, initialEndDate, price }) => {
  const setLoginUser = useSetRecoilState(loginState);
  const loginUser = useRecoilValue(loginState);
  const [dateRange, setDateRange] = useState([
    new Date(initialStartDate),
    new Date(initialEndDate),
  ]);
  const [showDatePicker, setShowDatePicker] = useState(false); // 
  const [nights, setNights] = useState(0); // 
  const [guestCount, setGuestCount] = useState(1); //  
  const [showGuestModal, setShowGuestModal] = useState(false); // 
  const [showConfirmationModal, setShowConfirmationModal] = useState(false); //    
  const [product, setProduct] = useState({
    image: "",
    title: "",
    description: "",
    name: "",
    price: 0,
  }); //  

  useEffect(() => {
    // server  getUser       !
    // true
    setLoginUser({
      email: 'gudrjsdn8825@naver.com',
      nickName: '',
      is_admin: false,
      is_logined: false,
    });

    //  (, )
    calculateNights(new Date(initialStartDate), new Date(initialEndDate));

    //     
    const fetchProductData = async () => {
      try {
        const response = await fetch("/productData.json"); //  API   
        if (!response.ok) {
          throw new Error("  ");
        }
        const data = await response.json();
        setProduct({
          image: data.image,
          title: data.title,
          description: data.description,
          name: data.hostName,
          price: data.price,
        });
      } catch (error) {
        console.error("  :", error);
      }
    };

    fetchProductData();
  }, []);

  //  
  const calculateNights = (start, end) => {
    if (start && end) {
      const diffTime = Math.abs(end - start);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      setNights(diffDays);
    } else {
      setNights(0);
    }
  };

  // ,          
  const handleDateChange = (dates) => {
    const [start, end] = dates;
    setDateRange([start, end]);
    if (end) {
      calculateNights(start, end);
      setShowDatePicker(false);
    }
  };

  //   
  const sendReservationRequest = async () => {
    const reservationData = {
      title,
      name,
      price,
      startDate: dateRange[0],
      endDate: dateRange[1],
      guestCount,
      totalPrice: price * nights,
    };

    try {
      const response = await fetch("/api/reservations", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(reservationData),
      });

      if (!response.ok) {
        throw new Error("  ");
      }

      const result = await response.json();
      console.log("  :", result);
      setShowConfirmationModal(true); //      
    } catch (error) {
      console.error("  :", error);
    }
  };

  const handleReservationRequest = () => {
    sendReservationRequest();
  };

  return (
    <>
      <Header user={loginUser} />
      <Container>
        <Section>
          <SectionTitle> </SectionTitle>
          <RequestContainer>
            {product.image ? (
              <img src={product.image} alt={product.title} />
            ) : (
              <ImagePlaceholder />
            )}
            <DetailContainer>
              <Title>{product.title}</Title>
              <Description>{product.description}</Description>
              <Name>: {product.name}</Name>
            </DetailContainer>
          </RequestContainer>
        </Section>
        <Section>
          <SectionTitle> </SectionTitle>
          <ReservationInfo
            dateRange={dateRange}
            nights={nights}
            showDatePicker={showDatePicker}
            setShowDatePicker={setShowDatePicker}
            handleDateChange={handleDateChange}
            guestCount={guestCount}
            setShowGuestModal={setShowGuestModal}
          />
        </Section>
        <Section>
          <SectionTitle> </SectionTitle>
          <ReservationPrice price={product.price} nights={nights} />
        </Section>
        <Section>
          <RefundPolicy>
            48     .  2   
               .
          </RefundPolicy>
          <Button onClick={handleReservationRequest}> </Button>
        </Section>
      </Container>
      <Footer user={loginUser} />
      {showGuestModal && (
        <GuestModal
          close={() => setShowGuestModal(false)}
          guestCount={guestCount}
          setGuestCount={setGuestCount}
        />
      )}
      {showConfirmationModal && (
        <ReservationModal
          message="  !"
          onClose={() => setShowConfirmationModal(false)}
        />
      )}
    </>
  );
};

//      
const today = new Date();
const tomorrow = new Date();
tomorrow.setDate(today.getDate() + 1);

ReservationPage.defaultProps = {
  title: " ",
  description: "[ ]     ",
  name: "",
  initialStartDate: today.toISOString().split("T")[0], //  
  initialEndDate: tomorrow.toISOString().split("T")[0], //  
  price: 120000,
};

export default ReservationPage;
