import axios from "axios";
import styled from "styled-components";
import { useState, useRef, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { PasswordRegex, PhoneNumberRegex } from "../account/Regex";
const FlexDiv = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
`;
const RequestBtn = styled.button`
  color: #f87878;
  background: #fff;
  height: 50px;
  width: 120px;
  border: 1px solid #f87878;
  border-radius: 15px;
  &:disabled {
    color: #bbb;
    border-color: #ddd;
  }
`;

const JoinInput = styled.input`
  border: 1px solid #ddd;
  background: #fff;
  border-radius: 15px;
  width: 100%;
  height: 50px;
  padding: 0 15px;
  ::placeholder {
    color: #666;
  }
  & + input {
    margin-top: 10px;
  }
`;
const JoinBtn = styled.button`
  background: #f87878;
  color: #fff;
  font-size: 16px;
  width: 100%;
  height: 50px;
  border: 0;
  border-radius: 15px;
  margin-top: 15px;
`;

const MessageDiv = styled.div`
  font-size: 12px;
  color: red;
  padding: 5px 0;
`;

const Join = () => {
  const [userInfo, setUserInfo] = useState({
    email: "",
    password: "",
    passwordCheck: "",
    name: "",
    phone: "",
    code: "",
    nickName: "",
  });
  const [passwordCheckError, setPasswordCheckError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [passwordError2, setPasswordError2] = useState("");
  const navigate = useNavigate();
  const emailCodeInput = useRef();
  const emailRequestBtn = useRef();

  //     
  const onChangeHandler = (e) => {
    setUserInfo((userInfoObj) => {
      const inputName = e.target.name;
      let inputValue = e.target.value;

      if (inputName === "phone") {
        //   
        // 000-0000-0000  
        inputValue = PhoneNumberRegex(inputValue);
      }

      if (inputName === "password") {
        if (inputValue.length > 0 && inputValue.length < 10) {
          setPasswordError("10  .");
        } else {
          setPasswordError("");

          /** , , ( )    /   */
          const [hasLetter, hasNumber, hasSpecialChar] = PasswordRegex(inputValue);
          const isValidCombination =
            [hasLetter, hasNumber, hasSpecialChar].filter(Boolean).length >= 2;
          // filter()   2   ,  

          if (!isValidCombination) {
            setPasswordError2(
              "//( ) , 2  "
            );
          } else {
            setPasswordError2("");
          }
        }
      }

      if (inputName === "passwordCheck") {
        if (userInfo.password !== inputValue) {
          setPasswordCheckError("  .");
        } else {
          setPasswordCheckError("");
        }
      }

      return { ...userInfoObj, [inputName]: inputValue };
    });
  };

  // password    passwordCheck  
  useEffect(() => {
    if (userInfo.password !== userInfo.passwordCheck) {
      setPasswordCheckError("  .");
    } else {
      setPasswordCheckError("");
    }
  }, [userInfo]);

  //    
  const onSubmitHandler = async (e) => {
    e.preventDefault();

    try {
      if (!!passwordCheckError || !!passwordError2 || !!passwordError) {
        alert(" .");
        return false;
      }
      await axios.post("http://localhost:3001/users", {
        email: userInfo.email,
        password: userInfo.password,
        name: userInfo.name,
        nickname: userInfo.nickName,
        phone: userInfo.phone,
      });

      navigate("/joinEnd");
    } catch(error) {
      alert(error.response.data.message);
    }

  };

  //     
  const onEmailRequestHandler = async (e) => {
    e.preventDefault();
    try {
      await axios.post("http://localhost:3001/users/verify", {
        email: userInfo.email,
      });
      e.target.disabled = true;
      emailCodeInput.current.readOnly = false;
      emailRequestBtn.current.disabled = false;
      alert(" .");
      //      
    } catch(error) {
      e.target.disabled = false;
      alert(error.response.data.message);
    };
  };


  //      
  const onEmailCheckHandler = async (e) => {
    //e.preventDefault();
    try {
      const response = await axios.post(
        "http://localhost:3001/users/verify/confirm",
        {
          email: userInfo.email,
          secret: userInfo.code,
        }
      );
      //      
      e.target.disabled = true;
      emailCodeInput.current.readOnly = true;
      alert(response.data.message);
    } catch(error) {
      //emailRequestBtn.current.disabled = false;
      e.target.disabled = false;
      alert(error.response.data.message);
    }

  };

  return (
    <form onSubmit={onSubmitHandler}>
      <FlexDiv>
        <JoinInput
          type="email"
          placeholder=""
          name="email"
          value={userInfo.email}
          onChange={(e) => {
            onChangeHandler(e);
          }}
          required
        />
        <RequestBtn
          type="button"
          onClick={onEmailRequestHandler}
          ref={emailRequestBtn}
        >
          
        </RequestBtn>
      </FlexDiv>
      <FlexDiv>
        <JoinInput
          type="text"
          placeholder=""
          name="code"
          value={userInfo.code}
          onChange={(e) => {
            onChangeHandler(e);
          }}
          ref={emailCodeInput}
          required
        />
        <RequestBtn type="button" onClick={onEmailCheckHandler}>
          
        </RequestBtn>
      </FlexDiv>

      <JoinInput
        type="text"
        placeholder=""
        name="name"
        value={userInfo.name}
        onChange={(e) => {
          onChangeHandler(e);
        }}
        required
      />

      <JoinInput
        type="password"
        placeholder=""
        name="password"
        value={userInfo.password}
        onChange={(e) => {
          onChangeHandler(e);
        }}
        required
      />
      {passwordError && <MessageDiv>{passwordError}</MessageDiv>}
      {passwordError2 && <MessageDiv>{passwordError2}</MessageDiv>}

      <JoinInput
        type="password"
        placeholder=" "
        name="passwordCheck"
        value={userInfo.passwordCheck}
        onChange={(e) => {
          onChangeHandler(e);
        }}
        required
      />
      {passwordCheckError && <MessageDiv>{passwordCheckError}</MessageDiv>}

      <JoinInput
        type="text"
        placeholder=""
        name="phone"
        maxLength={13}
        //   value undefined    undefined     .
        value={userInfo.phone || ""}
        onChange={(e) => {
          onChangeHandler(e);
        }}
        required
      />
      <JoinInput
        type="text"
        placeholder=""
        name="nickName"
        value={userInfo.nickName}
        onChange={(e) => {
          onChangeHandler(e);
        }}
      />
      <JoinBtn type="submit"></JoinBtn>
    </form>
  );
};

export default Join;
