import React, {useEffect, useState} from "react";
import styled from "styled-components";
import { tagArr } from "../../util/data/arrayStaticData";

const Container = styled.div`
    width: 100%;
    height: 60px;
    background-color: white;
    
    border-bottom: 2px solid #EEEEEE;
    padding-top: 10px;
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
`
const Direction = styled.div`
    width: 5%;
    right: 2%;
    top: 20%;
    font-size: 20px;
    font-weight: bold;
    position: absolute;

    @keyframes colorChange {
        0% { color: #333; }
        50% { color: #E61E51; }
        100% { color: #333; }
    }
    //  
    animation: colorChange 2s infinite;
`
const ItemDiv = styled.div`
    width: 90%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 5px;
    // x scroll 
    overflow-x: auto;
    // oneline
    flex-wrap: nowrap;
    //    	
	scroll-snap-type: x mandatory;
    /*    */
    -webkit-overflow-scrolling: touch;
    /*   */
    ::-webkit-scrollbar {
        display: none;
    }
    -ms-overflow-style: none;
    scrollbar-width: none; 
`

const Item = styled.div`
    width: 25%;
    height: 98%;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    //    ()
    scroll-snap-align: start;
    // + "  " (div > img  + div  flex-frow, shrink, basis( )  !)
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 75px;
    cursor: pointer;
`
const ItemTitle = styled.span`
    font-size: 12px;
    color: #797979;
`
const ItemImg = styled.img`
    width: 24px;
    height: 24px;
`


const Category = ({setCategory, setPage}) => {
    //     
    const [tag, setTag] = useState(tagArr[0]);

    // ()   setCategory  category  
    useEffect(() => {
        setCategory(tag);
    },[tag])

    // mainCatetory   
    const importAllImages = (v) => {
        return v.keys().map((key) => ({
            src: v(key),
            name: key.match(/[^/]+$/)[0], //   
          }));
    };
    const images = importAllImages(require.context('../../assets/icons/mainCategory', false, /\.(png|jpe?g|gif)$/));

    //    
    const sortedImages = images.sort((a, b) => {
        const aNumber = parseInt(a.name.split('_')[0], 10);
        const bNumber = parseInt(b.name.split('_')[0], 10);
        return aNumber - bNumber;
    });
    //      
    const onClickImage = (index) => {
        setTag(tagArr[index]);
        setPage((current) => {
            const newPage = {...current};
            newPage.page = 1;
            return newPage;
        });
    };

    return (
        <Container>
            <Direction>
            {">>"}
            </Direction>
            <ItemDiv>
                {sortedImages.map((v, i) => (
                    <Item key={i} onClick={() => onClickImage(i)} style={tag === tagArr[i] ? {borderBottom: "2px solid #333"} : {borderBottom: "none"}}>
                        <ItemImg src={v.src} />
                        <ItemTitle style={tag === tagArr[i] ? {fontWeight: "bold"} : {fontWeight: "500"}}>{tagArr[i]}</ItemTitle>
                    </Item>
                ))}
            </ItemDiv>
        </Container>
    );
}

export default Category;