import React, { useEffect } from "react";

const KakaoMap = () => {
  useEffect(() => {
    // Kakao Maps API   
    const script = document.createElement("script");
    script.async = true;
    script.src =
      "https://dapi.kakao.com/v2/maps/sdk.js?appkey=b27bdfb85b1124cb1f51e1859cd1da16&autoload=false";
    document.head.appendChild(script);

    script.onload = () => {
      // Kakao Maps API    
      if (window.kakao && window.kakao.maps) {
        window.kakao.maps.load(() => {
          const mapContainer = document.getElementById("map"); //   div
          const mapOption = {
            center: new window.kakao.maps.LatLng(37.5665, 126.978), //   
            level: 3, //   
          };

          //  
          const map = new window.kakao.maps.Map(mapContainer, mapOption);

          //   
          const markerPosition = new window.kakao.maps.LatLng(37.5665, 126.978);

          //  
          const marker = new window.kakao.maps.Marker({
            position: markerPosition,
          });

          //     
          marker.setMap(map);
        });
      } else {
        console.error("Kakao Maps script failed to load.");
      }
    };

    script.onerror = () => {
      console.error("Kakao Maps script failed to load.");
    };

    // Cleanup function to remove the script when the component unmounts
    return () => {
      document.head.removeChild(script);
    };
  }, []);

  return <div id="map" style={{ width: "100%", height: "500px" }}></div>;
};

export default KakaoMap;
