import React from "react";
import styled from "styled-components";

const SliderContainer = styled.div`
  width: ${(props) => props.size}px;
  height: ${(props) => props.size}px;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
`;
//  (  )
const SliderWrapper = styled.div`
  display: flex;
  transition: transform 0.5s ease-in-out;
  width: ${(props) =>
    props.size * props.imageCount}px; //  =    
  height: 100%;
`;
const SliderImage = styled.div`
  background-image: url(${(props) => props.imageUrl});
  background-size: cover;
  background-position: center;
  width: ${(props) => props.size}px;
  height: 100%;
`;
const ArrowButton = styled.button`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  border-radius: 50px;
  padding: 5px;
  cursor: pointer;
  transition: opacity 0.3s ease-in-out;
  opacity: 0.5;

  &:hover {
    opacity: 1; //     1
  }
`;
const PrevButton = styled(ArrowButton)`
  left: 5px;
  ${(props) => !props.show && "display: none;"}// show   
`;
const NextButton = styled(ArrowButton)`
  right: 5px;
  ${(props) => !props.show && "display: none;"}// show   
`;

const ImageSlider = ({
  imageUrls,
  currentIndex,
  setCurrentIndex,
  size = 111,
}) => {
  //    
  const handlePrevClick = (event) => {
    event.stopPropagation();
    setCurrentIndex(
      (prevIndex) => (prevIndex - 1 + imageUrls.length) % imageUrls.length
    );
  };

  //    
  const handleNextClick = (event) => {
    event.stopPropagation();
    setCurrentIndex((prevIndex) => (prevIndex + 1) % imageUrls.length);
  };

  return (
    <SliderContainer size={size}>
      <SliderWrapper
        size={size}
        imageCount={imageUrls.length}
        style={{ transform: `translateX(-${currentIndex * size}px)` }}
      >
        {imageUrls.map((imageUrl, index) => (
          <SliderImage key={index} imageUrl={imageUrl} size={size} />
        ))}
      </SliderWrapper>
      {imageUrls.length > 1 && (
        <>
          <PrevButton
            className="arrow-button"
            //   show 
            show={currentIndex > 0}
            onClick={handlePrevClick}
          >
            &lt;
          </PrevButton>
          <NextButton
            className="arrow-button"
            //    show 
            show={currentIndex < imageUrls.length - 1}
            onClick={handleNextClick}
          >
            &gt;
          </NextButton>
        </>
      )}
    </SliderContainer>
  );
};

export default ImageSlider;
