import React, { useRef, useEffect, useState, useCallback } from "react";
import styled from "styled-components";
import mainPostLoad from "../../api/mainPostLoad";
import main_no_data from '../../assets/images/main_no_data.png';
import OneItem from "./OneItem";

const Container = styled.div`
    width: 100%;
    height: 100%; // default
    margin-top: 20px;
    display: flex;
    gap: 30px;
    flex-direction: column;
    align-items: center;
`
//   (NoItem  )
const NoItemContainer = styled.div`
    padding-top: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
`
const NoItem = styled.div.attrs(props => ({
    style: {
        backgroundImage: `url(${props.$main_no_data})`
    }
}))`
    width: 100px;
    height: 100px;
`
const NoItemSpan = styled.span`
    font-size: 20px;
    color: #E61E51;
`

const Pagenation_div = styled.div`
    width: 100%;
    font-size: 17px;
    margin: 0 auto;
    margin-bottom: 100px;
`
const Pagenation_ul = styled.ul`
    width: 100%;
    height: 100%;
    // ul list     (padding-left 0)
    padding-left: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
`
const Pagenation_span = styled.span`
    cursor: pointer;
    font-weight: bold;
    transition: color 0.5s;
    color: #797979;
 
    &:hover {
        color: #E61E51;
    }
`
const Pagenation_li = styled.li`
    width: 10px;
    list-style: none;
    cursor: pointer;
    transition: color 0.5s;
    color: #797979;

    &:hover {
        color: #E61E51;
    }
`

const Items = ({page, setPage, startSearch, category}) => {
    //    
    const [posts, setPosts] = useState(null);
    
    //      search x, category x    
    // 1.      (  case1.     & case2. category    )
    // 2.         
    useEffect(() => {
        if(page && page.page === 1){
            mainPostLoad.getPostsPage({search: startSearch, category, mymode: false})
            .then(res => {
                setPage(res);
            });

            mainPostLoad.getPostsRead({nowpage: page.page, search: startSearch, category, mymode: false})
            .then(res => {
                setPosts(res);
            });
        } else if(page && page.page > 1) {
            mainPostLoad.getPostsRead({nowpage: page.page, search: startSearch, category, mymode: false})
            .then(res => {
                setPosts(res);
            });
        }        
    },[startSearch, category, page.page]);

    // page        
    // 5   
    //  2   2    
    const pagenationing = useCallback(() => {
        const pageArray = [];

        //   
        let remainpage = page.page;
        let count = 0;
        while((remainpage - count) % 2 !== 1){
            count++;
        }
        const startpage = page.page - count;

        //   
        remainpage = startpage + 1;
        if(remainpage > page.totalPage){
            remainpage = page.totalPage;
        }
        const lastpage = remainpage;

        for(let i = startpage;i <= lastpage; i++){
            pageArray.push(i);
        }
        return pageArray;
    },[page]);

    //    
    const pagenateHandle = (i) => {
        setPage((current) => {
            const newPage = {...current};
            newPage.page = i;
            return newPage;
        });

        window.scrollTo({
            top: 0,
            behavior: 'smooth'
          });   
    };
    
    //      5   
    const pagePrevHandle = () => {
        //    5 (5     )
        let i = page.page - page.perPage;
        if(i < 1){
            i = 1;
        }

        setPage((current) => {
            const newPage = {...current};
            newPage.page = i;
            return newPage;
        });

        window.scrollTo({
            top: 0,
            behavior: 'smooth'
          });
       
    };

    //      5   
    const pageNextHandle = () => {
        //    5 (5     )
        let i = page.page + page.perPage;
        if(i > page.totalPage){
            i = page.totalPage;
        }

        setPage((current) => {
            const newPage = {...current};
            newPage.page = i;
            return newPage;
        });

        window.scrollTo({
            top: 0,
            behavior: 'smooth'
          });
    };

    return (
        <Container>
            {posts && posts.map((v, i) => (
                <OneItem key={i} v={v} />
            ))}
            {posts && posts.length === 0 &&
                <NoItemContainer>
                    <NoItem $main_no_data={main_no_data}></NoItem>
                    <NoItemSpan>  </NoItemSpan>
                </NoItemContainer>  
            ||
                <Pagenation_div>
                    <Pagenation_ul>
                        <Pagenation_span onClick={pagePrevHandle}>{"<<"}</Pagenation_span>
                            {pagenationing().map((v,i) => {
                                return (
                                    <Pagenation_li key={i} onClick={() => pagenateHandle(v)} style={page.page === v ? {fontWeight: "bold", color: "#E61E51"} : {fontWeight: "400", color: "#797979"}}>{v}</Pagenation_li>
                                );
                            })}
                        <Pagenation_span onClick={pageNextHandle}>{">>"}</Pagenation_span>
                    </Pagenation_ul>
                </Pagenation_div>
            }
        </Container>
    );
};

export default Items;